.collab-container {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
  display: flex;
  flex-direction: column;
}

.card-subtitle {
  margin: 0 0 24px 0;
  padding: 0;
  font-size: 20px;
  line-height: 28px;
}

.card-content {
  margin: 0 0 32px 0;
  padding: 0;
  font-size: 16px;
  line-height: 24px;
  color: #606162;
  justify-content: center;
}

.card-viewoption {
  display: flex;
  flex-direction: row;
  position: relative;
  transition: transform 0.3s ease;
  transform: translateX(0px);
}

.card-viewoption::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 75px;
  height: 1px;
  background-color: #0076d5;
  transform: scale(0, 1);
}

.card-viewoption:hover::after {
  transform: scale(1, 1);
}

.option {
  display: flex;
  padding-right: 11.5px;
  font-style: normal;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #0076d5;
  margin-right: 0px;
  padding-top: 0.5px;
  padding-bottom: 2.5px;
  transition: margin-right padding-top 0.1s;
}

.option:hover {
  margin-right: 2px;
  padding-top: 0px;
}

.icon-greater {
  display: flex;
  margin-top: 4.5px;
  margin-left: 0px;
  padding-top: 1px;
  width: 5.5px;
  height: 10.98px;
  transition: margin-left 0.1s;
  fill: #0076d5;
}

.icon-greater:hover {
  margin-left: 2px;
}

.collab-btn {
  background-color: white;
  width: 217px;
  height: 56px;
  border: 1px solid #0076d5;
  box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #0076d5;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  transition: color background-color border-color 0.1s;
  align-self: center;
}

.collab-btn:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

/* For mobile phones: */
@media (min-width: 320px) {
  .collab-container-sub {
    margin: 48px 5% 0 5%;
    padding: 0;
    border-bottom: 1px solid #e6e7e8;
  }

  .collab-header {
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: 24px;
    line-height: 32px;
  }

  .collab-content {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    margin: 48px 0 0 0;
    padding: 0;
  }

  .collab-card {
    margin: 0 0 48px 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .collab-icons {
    margin: 24px 5% 48px 5%;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    column-gap: 80px;
    align-items: center;
    justify-content: center;
  }

  .collab-icon {
    margin: 24px 0 0 0;
    padding: 0;
    width: 40px;
    height: 40px;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .collab-container-sub {
    margin: 64px 10.42% 0 10.42%;
    padding: 0;
    border-bottom: 1px solid #e6e7e8;
  }

  .collab-header {
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: 32px;
    line-height: 40px;
  }

  .collab-content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 48px 0 0 0;
    padding: 0;
  }

  .collab-card {
    margin: 0 0 48px 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    width: calc((50%) - 12px);
  }

  .collab-icons {
    margin: 48px 10.42% 48px 10.42%;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    column-gap: 80px;
    align-items: center;
    justify-content: center;
  }

  .collab-icon {
    margin: 0;
    padding: 0;
    width: 40px;
    height: 40px;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .collab-container {
    width: auto;
    margin: 0 128px 0 128px;
  }

  .collab-container-sub {
    margin-top: 80px;
    margin-bottom: 0;
    margin-left: calc((10%) + 2.4px);
    margin-right: calc((10%) + 2.4px);
    border-bottom: 1px solid #e6e7e8;
  }

  .collab-header {
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: 32px;
    line-height: 40px;
  }

  .collab-content {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin: 64px 0 0 0;
    padding: 0;
  }

  .collab-card {
    margin: 0 0 64px 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    width: calc((33.33%) - 16px);
  }

  .collab-icons {
    margin-top: 64px;
    margin-bottom: 64px;
    margin-left: calc((10%) + 2.4px);
    margin-right: calc((10%) + 2.4px);
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    column-gap: 128px;
    align-items: center;
    justify-content: center;
  }

  .collab-icon {
    margin: 0;
    padding: 0;
    width: 60px;
    height: 60px;
  }
}
